import { Injectable } from '@angular/core';
import { Todo } from '../models/Todo';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  priority = [
    'None',
    'Low',
    'Medium',
    'High'
  ];
  todos: Todo[] = [
  ];
  constructor() { }
  getTodos() {
    return this.todos;
  }
  addTodo(todo: Todo) {
    this.todos.push(todo);
  }
  deleteTodo(todo: Todo) {
    let index;
    this.todos.filter(t => {
      if (t.task === todo.task) {
        index = this.todos.indexOf(todo);
      }
    });
    this.todos.splice(-1, index);
  }
}


